<template>
  <div>
    <div>
      <b-row class="">
        <b-col cols="9" xl="5" class="filterCard mb-2 ml-3">
          <b-col class="mb-2 mr-sm-2 mb-sm-0 alignleft title-fw">
            <label class="label mb-2 mr-sm-2 mb-sm-0">
              {{ $t("FILTER") }}
            </label>
          </b-col>
          <b-col xl="8" cols="12" class="ml-5 alignright">
            <b-input-group>
              <b-input-group-prepend>
                <b-button size="sm" variant="info" class="append-btn">{{
                  $t("USERMANAGEMENT.EXPENSEITEM")
                }}</b-button>
              </b-input-group-prepend>
              <DropDownSearch
                @selected_person_data="onChange"
                :searchList="expenseItemOptions"
                class="searchSelect1"
              ></DropDownSearch>
              <!-- <b-button
                @click="clear()"
                variant="primary"
                class="text-md mb-2 ml-2"
                size="sm"
                >{{ $t("CLEAR") }}</b-button
              > -->
            </b-input-group>
          </b-col>
        </b-col>
        <b-col cols="12" xl="4" class="mb-0">
          <b-button
            @click="goForCreate"
            variant="primary"
            class="text-md mb-2"
            >{{ $t("USERMANAGEMENT.ADDUSERMANAGEMENT") }}</b-button
          >
        </b-col>
      </b-row>
      <b-row> </b-row>
      <b-card id="settingsCard" no-body>
        <b-table
          ref="selectableTable"
          @change-page="pageChange"
          :bordered="false"
          :striped="false"
          :borderless="false"
          :fields="fieldss"
          :items="userData"
          :busy="isBusy"
          :per-page="limit"
          :total="total"
          :startPage="start"
          :outlined="true"
          :responsive="true"
          head-variant="light"
          tbody-tr-class=""
          fixed
          sticky-header
        >
          <!-- <template v-slot:cell(checkState)="item">
            <b-form-checkbox
              v-if="getCheckBoxControl(item)"
              :id="'checkbox-' + item.index"
              v-model="item.item.checkState"
              :name="'checkbox-' + item.index"
              @change="toggleSelectAll(item)"
            ></b-form-checkbox>
          </template> -->

          <template v-slot:cell(checkState)="item">
            <b-form-radio
              :id="'radio-' + item.index"
              v-model="item.checked"
              :name="'table-radio'"
              @change="toggleSelectAll(item)"
            ></b-form-radio>
          </template>
          <template v-slot:cell(ExpenseItem)="item">{{ item.value }}</template>
          <template v-slot:cell(Supplier)="item">{{ item.value }}</template>
          <template v-slot:cell(Place)="item">{{ item.value }}</template>
          <template v-slot:cell(RegisterdExpense)="item">{{
            item.value
          }}</template>
          <template v-slot:cell(PersonIncharge)="item">{{
            item.value
          }}</template>
          <template v-slot:cell(CreateDate)="item">{{ item.value }}</template>
          <template v-slot:cell(UpdateDate)="item">{{ item.value }}</template>
          <template v-slot:cell(Icon)="item">
            <b-button
              @click.prevent="goForUpdate(item)"
              variant="primary"
              class="Detailsbtn"
              >{{ $t("EDIT") }}</b-button
            >
          </template>
          <template v-slot:table-busy>
            <div class="text-center text-danger my-2">
              <b-spinner class="align-middle"></b-spinner>
            </div>
          </template>
        </b-table>
      </b-card>
    </div>
    <b-row>
      <b-col cols="12" xl="5" class="mt-2">
        <!-- <b-button @click="updateTaskData" variant="primary">{{
          $t("CHECKEDTASKBATCH")
        }}</b-button> -->

        <b-button
          @click="deleteSelectedSupplier"
          :disabled="!checkedItems.length > 0"
          variant="primary"
          class="text-md"
          >{{ $t("USERMANAGEMENT.DELETECHECKEDUSERMANAGEMENT") }}</b-button
        >
      </b-col>
      <b-col class="mt-2" cols="12" xl="5">
        <b-pagination
          v-model="currentPage"
          :total-rows="totalRows"
          :per-page="limit"
          size="sm"
          align="center"
        >
          <template v-slot:prev-text>{{ $t("PREVIOUS") }}</template>
          <template v-slot:next-text>{{ $t("NEXT") }}</template>
          <template v-slot:page="{ page, active }">
            <strong v-if="active">{{ page }}</strong>
            <i v-else>{{ page }}</i>
          </template>
        </b-pagination>
        <!-- <b-pagination
          id="message-list"
          v-if="lists.length != 0"
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          align="center"
        ></b-pagination> -->
      </b-col>
      <b-col cols="12" xl="2" class="mt-2 text-center">
        <!-- <div class="countList">
          <label>表示</label>&nbsp;&nbsp;
          <label>50 | </label>
          <b-link href="javascript:;">100 | </b-link>
          <b-link href="javascript:;">150</b-link>
        </div> -->
        <div class="countList">
          <label>{{ $t("MEANS") }}</label
          >&nbsp;&nbsp;
          <template v-for="(page, index) in pageOptions">
            <b-link :key="index" v-if="limit == page" href="javascript:;">
              {{ page }}
              <span v-if="index != pageOptions.length - 1">&nbsp;|&nbsp;</span>
            </b-link>
            <b-link
              :key="index"
              @click="perPageChange(page)"
              v-else
              href="javascript:;"
            >
              <u>{{ page }}</u>
              <span v-if="index != pageOptions.length - 1">&nbsp;|&nbsp;</span>
            </b-link>
          </template>
        </div>
      </b-col> </b-row
    ><br />
  </div>
</template>

<script>
import api from "@/shared/api";
import i18n from "@/plugins/i18n";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import DropDownSearch from "@/components/DropDownSearch";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "SupplierList",
  components: { DropDownSearch },
  data() {
    return {
      selectBoxReload: 0,
      expenseItemOptions: [
        {
          value: { accounts_name_cd: "0001", expense_item_cd: "001" },
          text: "AAA",
        },
        {
          value: { accounts_name_cd: "0002", expense_item_cd: "002" },
          text: "BBB",
        },
        {
          value: { accounts_name_cd: "0003", expense_item_cd: "003" },
          text: "CCC",
        },
      ],
      start: 0,
      total: 0,
      checkedItems: [],
      selectedExpenseItem: {
        accounts_name_cd: "",
        expense_item_cd: "",
      },
      options: [
        { value: "01", text: this.$t("UNPROCESSED") },
        { value: "02", text: this.$t("PROCESSING") },
        { value: "03", text: this.$t("PROCESSED") },
      ],
      limit: 50,
      currentPage: 1,
      pageOptions: [50, 100, 200],
      userData: [],
      tableData: [
        {
          Supplier: "",
          Place: "",
          RegisterdExpense: "",
          CreateDate: "",
          UpdateDate: "",
          PersonIncharge: "",
        },
      ],
      isBusy: false,
    };
  },
  computed: {
    totalRows() {
      return 10;
    },
    fieldss() {
      return [
        {
          key: "checkState",
          label: "",
          class: "text-center check-normal-width",
        },
        {
          key: "expense_item_nm",
          label: this.$t("USERMANAGEMENT.EXPENSEITEM"),
          class: "text-left  normal-width",
        },
        {
          key: "user_management_title",
          label: this.$t("SUBJECT"),
          class: "text-left  normal-width",
        },
        {
          key: "person_name",
          label: this.$t("USERMANAGEMENT.REGISTEREDPERSON"),
          class: "text-center  title-normal-width",
        },
        {
          key: "del_flg",
          label:
            this.$t("USERMANAGEMENT.SHOW") +
            "/" +
            this.$t("USERMANAGEMENT.HIDE"),
          class: "text-center  normal-width",
        },
        {
          key: "create_dt",
          label: this.$t("EXPENSEREDUCTION.CreateDate"),
          class: "text-center  date-normal-width",
        },
        {
          key: "update_dt",
          label: this.$t("UpdateDate"),
          class: "text-center  date-normal-width",
        },
        // {
        //   key: "PersonIncharge",
        //   label: this.$t("EXPENSEREDUCTION.PersonIncharge"),
        //   class: "text-center"
        // },
        {
          key: "Icon",
          label: "",
          class: "text-center  title-normal-width",
        },
      ];
    },
  },
  watch: {
    currentPage() {
      this.pageChange(this.currentPage);
    },
    startPage() {
      this.currentPage = this.startPage + 1;
    },
  },
  mounted() {
    this.updateTaskData();
    this.getExpenseItemList();
  },
  methods: {
    clear() {
      this.selectedExpenseItem = { accounts_name_cd: "", expense_item_cd: "" };
      // this.selectBoxReload = this.selectBoxReload + 1;
      this.updateTaskData();
    },
    goForCreate() {
      this.$router.push({
        name: "user_management_edit_add",
      });
    },
    goForUpdate(item) {
      item.item = Object.assign(item.item, { forUpdate: true });
      this.$router.push({
        name: "user_management_edit_add",
        params: { item: item.item },
      });
    },
    getCheckBoxControl() {
      return true;
    },
    async deleteSelectedSupplier() {
      this.$swal({
        // title: i18n.t("USERMANAGEMENTDELETEMSG"),
        text: i18n.t("USERMANAGEMENTDELETEMSG"),
        type: "warning",
        showCancelButton: true,
        confirmButtonText: i18n.t("YESDELETE"),
        cancelButtonText: i18n.t("NODELETE"),
      }).then(async (result) => {
        if (result.value) {
          let compiledArray = {
            conditions: [
              {
                key: "user_management_no",
                value: this.checkedItems,
              },
            ],
            update_id: localStorage.getItem("personID"),
          };
          const { data } = await BasicInfoApis.postAPI(
            endPoint.MASTERSOFTDELETEMUSERMANAGEMENT,
            compiledArray
          );

          if (data.success) {
            this.$swal({ type: "success", text: i18n.t("DELETED") });
            this.checkedItems = [];
            this.updateTaskData();
          } else {
            this.$swal({ type: "error", text: i18n.t("NOTDELETED") });
          }
        }
      });
    },
    toggleSelectAll(item) {
      item.item.checkState = !item.item.checkState;
      let checkedItems = this.userData.filter((checkedItem) => {
        if (checkedItem.checkState == true) {
          return checkedItem;
        }
      });

      this.checkedItems = checkedItems.map((item) => item.user_management_no);
    },
    getDate(date) {
      if (!date) {
        return "";
      }
      return (
        date.slice(0, 4) + "/" + date.slice(5, 7) + "/" + date.slice(8, 10)
      );
    },
    async getExpenseItemList() {
      const { data } = await BasicInfoApis.postAPI(endPoint.READMEXPENSEITEM, {
        fieldlist: ["accounts_name_cd", "expense_item_cd", "expense_item_nm"],
      });
      if (data.success) {
        let responseData = data.data;
        let expenseListData = [];
        if (responseData.length) {
          responseData.forEach((item) => {
            let iterateExpenseItem = {
              text: item.expense_item_nm ? item.expense_item_nm : "",
              value: {
                accounts_name_cd: item.accounts_name_cd
                  ? item.accounts_name_cd
                  : "",
                expense_item_cd: item.expense_item_cd
                  ? item.expense_item_cd
                  : "",
              },
            };
            expenseListData.push(iterateExpenseItem);
          });
          this.expenseItemOptions = expenseListData;
        } else {
          this.expenseItemOptions = [];
        }
      } else {
        this.expenseItemOptions = [];
      }
    },
    getRowCount() {
      return this.items.length;
    },
    pageChange(page) {
      this.start = page - 1;
      this.updateTaskData();
    },
    perPageChange(page) {
      this.limit = page;
      this.updateTaskData();
    },
    onChange(item) {
      console.log("HHHHHHHHH", this.selectedExpenseItem, item);
      if (item.text) {
        this.selectedExpenseItem = item.value;
      } else {
        this.selectedExpenseItem = {
          accounts_name_cd: "",
          expense_item_cd: "",
        };
      }

      this.updateTaskData();
    },
    async updateTaskData() {
      this.isBusy = true;

      this.userData = await api.post(endPoint.USERMANAGEMENTLIST, {
        expense_item_cd: this.selectedExpenseItem.expense_item_cd,
        accounts_name_cd: this.selectedExpenseItem.accounts_name_cd,
        limit: this.limit,
        offset: this.start * 10,
        paginate: true,
      });

      this.isBusy = false;
      this.total = this.userData.data.data.total;
      console.log("ID", localStorage.getItem("personID"));
      this.userData = this.userData.data.data.userManagementList.map(
        (udata) => {
          udata["checkState"] = false;
          udata["create_dt"] = this.getDate(udata.create_dt);
          udata["update_dt"] = this.getDate(udata.update_dt);
          udata["del_flg"] =
            udata.del_flg == true
              ? this.$t("USERMANAGEMENT.HIDE")
              : this.$t("USERMANAGEMENT.SHOW");
          return udata;
        }
      );
    },
  },
};
</script>
<style scoped lang="scss">
.searchSelect1 {
  position: relative !important;
  flex: 1 1 auto !important;
  width: 1% !important;
  margin-bottom: 0 !important;
  height: 28px !important;
}
.append-btn {
  min-width: 10px;
  border: none !important;
  background: none !important;
}
.input-group-text {
  background-color: none;
  border: none;
}

.filterCard {
  padding-top: 2px;
  padding-bottom: 4px;
  border: 1px dashed;
}
.alignleft {
  margin-left: -1.5rem !important;
}
.alignright {
  margin-left: 5rem !important;
}
.normal-width {
  width: 80px !important;
}
.title-normal-width {
  width: 110px !important;
}
.date-normal-width {
  width: 110px !important;
}
.check-normal-width {
  width: 80px !important;
  padding-top: 15px !important;
}
.custom-select {
  height: 30px;
  border-color: rgb(163, 163, 163);
}

.text-sm {
  font-size: 10px;
}
@media only screen and (max-width: 1070px) {
  .alignright {
    margin-left: 0px !important;
  }
  .ml-5 {
    margin-left: 0px !important;
  }
}
</style>
<style lang="scss">
.normal-width {
  width: 80px !important;
}
.title-normal-width {
  width: 110px !important;
}
.date-normal-width {
  width: 110px !important;
}
.check-normal-width {
  width: 80px !important;
  padding-top: 15px !important;
}
</style>
<style lang="scss">
.custom-control-label {
  font-size: 11px !important;
  line-height: 21px !important;
}
.custom-control-inline {
  margin-right: 0.5rem !important;
}
.ui.search.selection.dropdown > input.search,
.ui.search.selection.dropdown > span.sizer {
  line-height: 1.21428571em !important;
  padding: 0.175rem 0.75rem !important;
  height: 35px;
}
.ui.selection.dropdown {
  border: 1px solid #a9a6a7;
  border-radius: 0px;
  height: 30px !important;
  padding: 0.175rem 0.75rem !important;
}
.ui.selection.dropdown:focus {
  color: #5c6873 !important;
  background-color: #fff !important;
  border-color: #0f61cb !important;
  outline: 0 !important;
  box-shadow: 0 0 0 0.2rem rgba(6, 40, 84, 0.25) !important;
}
.ui.selection.active.dropdown:hover {
  border-color: #0f61cb !important;
  box-shadow: 0 0 0 0.2rem rgba(6, 40, 84, 0.25) !important;
}
.ui.search.dropdown > .text {
  padding-top: 7px;
}
</style>